@font-face {
    font-family: 'capture_itregular';
    src: url('../fonts/Capture-it/capture_it-webfont.eot');
    src: url('../fonts/Capture-it/capture_it-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/Capture-it/capture_it-webfont.woff2') format('woff2'),
         url('../fonts/Capture-it/capture_it-webfont.woff') format('woff'),
         url('../fonts/Capture-it/capture_it-webfont.ttf') format('truetype'),
         url('../fonts/Capture-it/capture_it-webfont.svg#capture_itregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

.capture_itregular {
    font-family : 'capture_itregular';
    font-weight : 200;
    color       : #0ccac4;
}